<template>
    <div id="container"></div>
</template>

<script>
import * as Three from 'three'


export default {
  name: 'ThreeTest',
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mesh: null
    }
  },
  methods: {
    init: function() {
        let container = document.getElementById('container');
        
        this.camera = new Three.PerspectiveCamera(20, container.clientWidth/container.clientHeight, 0.01, 10);
        this.camera.position.z = 1;

        this.scene = new Three.Scene();

        const geometry = new Three.BoxGeometry( 0.2, 0.2, 0.2 );
        const edgesGeometry = new Three.EdgesGeometry( geometry );
        const material = new Three.LineBasicMaterial();

        this.mesh = new Three.LineSegments( edgesGeometry, material );

        this.scene.add(this.mesh);

        this.renderer = new Three.WebGLRenderer({antialias: true, alpha:true});
        this.renderer.setSize(container.clientWidth, container.clientHeight);
        // let box = document.getElementsByTagName('body')[0];
        // this.renderer.setPixelRatio(this.box.clientWidth, this.box.clientHeight);
        container.appendChild(this.renderer.domElement);

    },
    animate: function() {
        requestAnimationFrame(this.animate);
        this.mesh.rotation.x += 0.01;
        this.mesh.rotation.y += 0.02;
        this.renderer.render(this.scene, this.camera);
    }
  },
  mounted() {
      this.init();
      this.animate();
  }
}
</script>

<style scoped>
    #container {
        background-color: transparent !important;
        height: 200px;
        width:200px;
    }

</style>