<template>
  <div class="container">
    <h2 class="pt-5">Stock Viewer</h2>

    <div class="row pt-5 justify-content-center">
      <div class="col-12 col-lg-7">
        <form @submit.prevent="handleSubmit">
          <div class="row justify-content-center">
            <div class="col-md-3 col-3 form-group">
              <label for="stockTicker" class="control-label form__label"
                >Ticker</label
              >
              <input
                type="text"
                name="stockTicker"
                v-model="stockTicker"
                id="stockTicker"
                class="form-control"
              />
            </div>
            <div class="col-md-3 col-3 form-group">
              <label for="endDate" class="control-label form__label"
                >Date</label
              >
              <input
                type="text"
                name="endDate"
                v-model="endDate"
                id="endDate"
                class="form-control"
              />
            </div>
            <div class="col-md-2 col-2 form-group">
              <label for="lookback" class="control-label form__label"
                >Lookback</label
              >
              <input
                type="text"
                name="lookback"
                v-model="lookback"
                id="lookback"
                class="form-control"
              />
            </div>
            <div class="col-2">
              <bbutton
                type="submit"
                class="btn-primary mt-4 contact-button"
                variant="primary"
              >
                <span class="button-text">SUBMIT</span>
              </bbutton>
            </div>
          </div>
        </form>
      </div>
    </div>

    <div class="row pt-3" v-show="submitted">
      <highcharts style="text='white'"
        :constructor-type="'stockChart'"
        :options="chartOptions"
      ></highcharts>
    </div>
  </div>
</template>

<script>
import { Chart } from "highcharts-vue";
import Highcharts from "highcharts";
import exportingInit from "highcharts/modules/exporting";
import stockInit from "highcharts/modules/stock";
import axios from "axios";
import { BButton } from "bootstrap-vue";

stockInit(Highcharts);
exportingInit(Highcharts);

export default {
  props: {
    partsdata: {
      type: Array,
    },
  },

  components: {
    highcharts: Chart,
    bbutton: BButton,
  },

  data() {
    return {
      stockTicker: "",
      endDate : "",
      lookback: "",
      submitted: false,
      API_res: "",
      formResponse: 0,

      chartOptions: {
       
        credits: {
          enabled: false,
        },
     
        plotOptions: {
          backgroundColor: null,
          series: {
            lineColor: "#03e9f4",
            marker: {
              radius: 4,
              lineColor: "#03e9f4",
              lineWidth: 1,
            },
            navigator :{
              lineColor: "#03e9f4",
            }
          },
        },
        series: [
          {
            // data: this.parsedStockData,
            data: undefined
          },
        ],
      },
    };
  },

   methods: {
    handleSubmit() {
      

      const api =
        "https://l5wcjczsdb.execute-api.eu-west-1.amazonaws.com/stage/get-data";
      const headers = {
        "Content-Type": "application/json",
        "x-api-key": "cQHztZhgKS1tuZhlalE0t1Zr51CFRAdP9WFiMhoQ",
      };

      var params = {
        date: this.endDate,
        lookback: parseInt(this.lookback),
        ticker: this.stockTicker,
      
      };

      axios
        .post(api, params, { headers })
        .then((response) => {
          console.log("API SUCCESS");
          var parsedStockPrices = this.parseStockData(response.data.body)
          this.chartOptions.series[0].data = parsedStockPrices


        })
        .catch((e) => {
          console.log(e)
          console.log("API het gekak");
          this.API_res = e
        });   
    },
    dates_as_int(date) {
      return new Date(date).getTime();
    },
    parseStockData (stockObject) {
      let series = []
      stockObject = JSON.parse(stockObject);
      for (const item of stockObject) {
        series.push([this.dates_as_int(item.price_date), item.close_price])
      }
      this.submitted = true;
      return series
    },

  },

};
</script>

